import React, { Component } from 'react';
import './App.css';
import InputForm from "./components/InputForm";
import ResultView from './components/ResultView';

class App extends Component {
  render = () => (
    <div>
      <h2>Myanmar Vehicle Import Tax Calculator</h2>
      <div>
        <InputForm/>
        <ResultView/>
      </div>
      <div>
        <div className="col-md-10">

          <div className="col-md-6 col-md-offset-3">
            <p>Tax calculation methods are inspired from:
              <ul>
                <li><a href="https://www.facebook.com/DistinctTrading/">Distinct Trading</a>'s tax calculation examples.</li>
                <li><a href="https://www.facebook.com/japanonlinecarauction/">JOCA</a>'s tax calculation examples.</li>
                <li><a href="https://www.facebook.com/moezacauto/">MoeZacAuto</a>'s tax calculation excel file.</li>
              </ul>
            </p>
          </div>

        </div>
      </div>
    </div>
  );
}

export default App;



