export const TaxModes = Object.freeze({
  "Default": 1,
  "GovernmentPermit": 2, // Government Employee
})

export const TaxModesNames = Object.freeze({
  "Default": "ပုံမှန်",
  "GovernmentPermit": "ပါမစ်",
})

class TaxCalculator {

  constructor(mode, av, rate, USD2MMK, vehicleValueInMMK, slipValueInMMK, fixed) {
    this.mode = mode
    this.av = av - 0;
    this.rate = rate;
    this.USD2MMK = USD2MMK;
    this.vehicleValueInMMK = vehicleValueInMMK;
    this.slipValueInMMK = slipValueInMMK;
    this.fixed = fixed;
  }

  customDuty() {
    if (this.mode === TaxModes.GovernmentPermit) { return 0 }
    return this.av * (this.rate.customDuty/100);
  }

  specialGoodsTax() {
    return (this.av + this.customDuty()) * (this.rate.specialGoodsTax/100);
  }

  commercialTax() {
    return (this.av + this.customDuty() + this.specialGoodsTax()) * (this.rate.commercialTax/100);
  }

  kaNyaNaTax() {
    if (this.mode === TaxModes.GovernmentPermit) { return 0 }
    return (this.av * 0.8333) * (this.rate.kaNyaNaTax/100);
  }

  kaNyaNaTaxInMMK() {
    return this.kaNyaNaTax() * this.USD2MMK;
  }

  sateKanTax() {
    return this.customDuty() + this.specialGoodsTax() + this.commercialTax();
  }

  sateKanTaxInMMK() {
    return this.sateKanTax() * this.USD2MMK;
  }

  taxableinMMK() {
    return (this.av * this.USD2MMK) + this.sateKanTaxInMMK();
  }

  incomeTaxRate() {
    if (this.fixed === true) {
      return 3;
    }

    const lakhs = 100000

    const taxableinMMK = this.taxableinMMK();
    if (taxableinMMK > 30000 * lakhs) {
      return 30;
    } else if (taxableinMMK > 10000 * lakhs) {
      return 15;
    } else if (taxableinMMK > 3000 * lakhs) {
      return 10;
    } else if (taxableinMMK > 1000 * lakhs) {
      return 5;
    } else {
      return 3;
    }
  }

  incomeTax() {
    return (this.incomeTaxRate()/100) * this.taxableinMMK();
  }

  totalTaxesMMK() {
    return this.sateKanTaxInMMK() + this.kaNyaNaTaxInMMK() + this.incomeTax();
  }

  totalTaxesUSD() {
    return this.totalTaxesMMK() / this.USD2MMK;
  }

  grandTotalMMK() {
    return this.totalTaxesMMK() + this.vehicleValueInMMK + this.slipValueInMMK;
  }

  grandTotalUSD() {
    return this.grandTotalMMK() / this.USD2MMK;
  }
}

export default TaxCalculator;


