import React, { Component } from 'react'
import TextInput from './TextInput.js'
import * as Rates from '../lib/Rates.js'
import 'react-select/dist/react-select.css'
import avList from './../data/av-all.json'
import Fuse from 'fuse.js'
import { connect } from 'react-redux'
import { setFormFields } from '../actions'
import { TaxModes, TaxModesNames } from '../lib/TaxCalculator.js'

class InputForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            mode: TaxModes.Default,
            rateClass: "1500",
            rate: {
                customDuty: 0,
                specialGoodsTax: 0,
                commercialTax: 0,
                kaNyaNaTax: 0,
            },
            av: 5000,
            USD2MMK: 1400,
            slipValue: 0,
            carOrigin: 'carFromUSD',
            avSeach: "Search by Model & Year...",
        };

        this.rateClasses = [
            {class: Rates.Rate0, name:"0", label:"1350cc အောက်"},
            {class: Rates.Rate1350, name:"1350", label:"1351cc-1500cc"},
            {class: Rates.Rate1500, name:"1500", label:"1501cc-2000cc"},
            {class: Rates.Rate2000, name:"2000", label:"2001cc-4000cc"},
            {class: Rates.Rate4000, name:"4000", label:"4001cc-5000cc"},
            {class: Rates.Rate5000, name:"5000", label:"5001cc အထက်"},
            {class: Rates.RatePickUp, name:"PickUp", label:"ပစ်ကပ်"},
            {class: Rates.RateElectric, name:"Electric", label:"လျှပ်စစ်ကား"},
        ];

        this.avArray = Object.keys(avList).map(function(key) {
            return { label: key, value: avList[key]};
        }) ;

        this.handleRateClassChange = this.handleRateClassChange.bind(this);
        this.handleRateChange = this.handleRateChange.bind(this);
    }

    getOptions(input, callback) {

        let result = [];

        if (input) {

            const options = {
                shouldSort: true,
                tokenize: true,
                matchAllTokens: true,
                findAllMatches: true,
                threshold: 0.3,
                location: 0,
                distance: 200,
                maxPatternLength: 32,
                minMatchCharLength: 2,
                keys: [
                    "label"
                ]
            };

            let fuse = new Fuse(this.avArray, options); // "list" is the item array
            result = fuse.search(input.trim());

            result = result.slice(0, 10);
        }

        callback(null, {options: result, complete: true});
    }

    getRate(rateClass) {
        const selectedRateClass = rateClass;

        let rate = 0;
        for (let c of this.rateClasses) {
            if (c.name === selectedRateClass) {
                rate = new (c.class)();
            }
        }

        return rate;
    }

    reportFieldsUpdate() {
        let { mode, av, rate, USD2MMK, slipValue } = this.state;

        av -= 0
        slipValue -= 0

        const vehicleValueInMMK = av * USD2MMK;

        const newFields = { mode, av, rate, USD2MMK, vehicleValueInMMK, slipValue };

        const { onFormFieldDidUpdate } = this.props;

        onFormFieldDidUpdate(newFields);
    }

    resetRates(rateClass) {
        const rates = this.getRate(rateClass);
        let object = {
              customDuty: rates.customDuty,
              specialGoodsTax: rates.specialGoodsTax,
              commercialTax: rates.commercialTax,
              kaNyaNaTax: rates.kaNyaNaTax
        }
        this.setState({rate: object})
    }

    componentDidMount() {
        this.reportFieldsUpdate();
        this.resetRates(this.state.rateClass);
    }

    componentDidUpdate() {
        this.reportFieldsUpdate();
    }

    handleRateClassChange(e) {
        this.setState({ rateClass: e.target.value })
        this.resetRates(e.target.value);
    }

    handleRateChange(kv) {
        let object = {...this.state.rate, ...kv}
        this.setState({rate: object})
    }

    render() {
        const selectedRateClass = this.state.rateClass;

        const rate = this.state.rate;

        return (
            <div className="col-md-5">
                <form className="form">
                    <div className="form-group">
                        <h4>၁။ Engine Power ရွေးပါ</h4>
                        <div onChange={this.handleRateClassChange}>
                            {this.rateClasses.map((rc)=>{
                                return (<div className="radio col-md-12"><label><input type="radio" value={rc.name} id={rc.name} checked={selectedRateClass === rc.name} />{rc.label}</label></div>)
                            })}
                        </div>
                    </div>
                    <a className="btn btn-primary" data-toggle="collapse" href="#taxRates" aria-expanded="false" aria-controls="taxRates">
                        နှုံးထားများ
                    </a>
                    <p>
                        <b>** အကောက်ခွန် 01-08-2020 နှုန်းအသစ်ဖြစ်သည် **</b><br/>
                        <b>** ကညန အခွန် 07-08-2020 နှုန်းအသစ်ဖြစ်သည် **</b>
                    </p>
                    <div id="taxRates" className="collapse" style={{fontSize: '.85em'}}>
                        <TextInput id="customDuty" suffix="%" type="text" label="အကောက်ခွန်" value={rate.customDuty} onChange={ val => this.handleRateChange({customDuty: val}) } />
                        <TextInput id="specialGoodsTax" suffix="%" type="text" label="အထူးကုန်စည်ခွန်" value={rate.specialGoodsTax} onChange={ val => this.handleRateChange({specialGoodsTax: val}) } />
                        <TextInput id="commercialTax" suffix="%" type="text" label="ကုန်သွယ်ခွန်" value={rate.commercialTax} onChange={ val => this.handleRateChange({commercialTax: val}) } />
                        <TextInput id="kaNyaNaTax" suffix="%" type="text" label="ကညန" value={rate.kaNyaNaTax} onChange={ val => this.handleRateChange({kaNyaNaTax: val}) } />
                        <hr/>
                    </div>

                    <div>
                        <h4>၂။ အောက်ပါတန်ဖိုးများကိုဖြည့်ပါ</h4>

                        <label>စည်းကြပ်တန်ဖိုး AV/CIF</label>

                        <div className="row">
                            <div className="col-md-6">
                                <TextInput id="av" prefix="$" label="" type="text" value={this.state.av} onChange={v => this.setState({ av: v })} />
                            </div>
                        </div>
                        <TextInput id="slipValue" prefix="ကျပ်" type="text" label="အပ်ကား (စလစ်)" value={this.state.slipValue} onChange={v => this.setState({ slipValue: v })} />
                        <div className="row">
                            <div className="col-md-6">
                                <TextInput id="USD2MMK" type="text" label="USD to MMK" value={this.state.USD2MMK} onChange={v => this.setState({ USD2MMK: v })} />
                            </div>
                        </div>
                    </div>

                    <div>
                        <h4>၃။ အခွန်ကောက်ပုံစံ</h4>
                        <div onChange={e => this.setState({ mode: + e.target.value })}>
                            {Object.keys(TaxModes).map((modeName)=>{
                                const modeValue = TaxModes[modeName]
                                return (<div className="radio col-md-12"><label><input type="radio" value={modeValue} id={modeValue} checked={this.state.mode === modeValue} />{TaxModesNames[modeName]}</label></div>)
                            })}
                        </div>
                    </div>
                </form>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onFormFieldDidUpdate: fields => {
            dispatch(setFormFields(fields));
        }
    }
};

InputForm = connect(null, mapDispatchToProps)(InputForm);

export default InputForm;


